<template>
  <div>
    <h1 style="margin-bottom: 30px;"></h1>
  <el-form ref="form" :model="form" label-width="150px" v-if="form" :rules="rules">
      <el-form-item :label="item.name+''" v-for="item,index in fromArr" :key="index" :prop="item.key">
         <el-date-picker v-if="item.type==='date'"
        v-model="form[item.key]"
      type="date" style="    width: 100%;"
      placeholder="">
    </el-date-picker>
          <el-input  v-if="item.type==='number'" maxlength="30" v-model="form[item.key]" oninput ="value=value.replace(/[^0-9.]/g,'')"></el-input>
           <el-select v-model="form[item.key]" placeholder="" v-if="item.type==='select'" class="w100">
            <el-option
              v-for="item in item.Arr"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
           <el-select v-model="form[item.key]" placeholder="" v-if="item.type==='selecteun'" class="w100">
            <el-option
              v-for="item in item.eun"
              :key="item"
              :label="item"
              :value="item">
            </el-option>
          </el-select>
          <el-input maxlength="30"  v-if="item.type===undefined" v-model="form[item.key]" ></el-input>
  </el-form-item>
  </el-form>
  <div style="    text-align: center;">
  <el-button class="btn_l" size="small" @click="BtnBack"></el-button>
  <el-button type="primary" size="small" class="btn_r" @click="BtnClick"></el-button>
  </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      rules: {
        appellation: [{ required: true, message: '', trigger: 'change' }],
        age: [{ required: true, message: '', trigger: 'change' }],
        sex: [{ required: true, message: '', trigger: 'change' }],
        height: [{ required: true, message: '', trigger: 'change' }],
        nation: [{ required: true, message: '', trigger: 'change' }],
        nativePlace: [{ required: true, message: '', trigger: 'change' }],
        boardingPlace: [{ required: true, message: '', trigger: 'change' }],
        graduationSchool: [{ required: true, message: '', trigger: 'change' }],
        graduationTime: [{ required: true, message: '', trigger: 'change' }],
        certificateLevel: [{ required: true, message: '', trigger: 'change' }],
        education: [{ required: true, message: '', trigger: 'change' }],
        currentResidence: [{ required: true, message: '', trigger: 'change' }],
        experienceSailing: [{ required: true, message: '', trigger: 'change' }],
        jobApplication: [{ required: true, message: '', trigger: 'change' }],
        major: [{ required: true, message: '', trigger: 'change' }],
        maritalStatus: [{ required: true, message: '', trigger: 'change' }],
        monthlySalary: [{ required: true, message: '', trigger: 'change' }],
        originalPosition: [{ required: true, message: '', trigger: 'change' }],
        politicalOutlook: [{ required: true, message: '', trigger: 'change' }],
        requiredGross: [{ required: true, message: '', trigger: 'change' }],
        routeRequirements: [{ required: true, message: '', trigger: 'change' }],
        shipRequirements: [{ required: true, message: '', trigger: 'change' }],
        technicalSkill: [{ required: true, message: '', trigger: 'change' }],
        userStatus: [{ required: true, message: '', trigger: 'change' }]

      },
      fromArr: [
        { key: 'appellation', name: '' },
        { key: 'age', name: '', type: 'number' },
        { key: 'sex', name: '', type: 'selecteun', eun: ['', ''] },
        { key: 'height', name: '', type: 'number' },
        { key: 'nation', name: '' },
        { key: 'nativePlace', name: '' },
        { key: 'boardingPlace', name: '', type: 'selecteun', eun: ['', '', '', '', '', ''] },
        { key: 'graduationSchool', name: '' },
        { key: 'graduationTime', name: '', type: 'date' },
        { key: 'certificateLevel', name: '' },
        { key: 'education', name: '' },
        { key: 'currentResidence', name: '' },
        { key: 'experienceSailing', name: '' },
        {
          key: 'jobApplication',
          name: '',
          type: 'selecteun',
          eun: [
            '',
            '',
            '',
            '',
            '',
            '',
            '',
            '',
            '',
            '',
            '',
            '',
            '',
            '',
            '',
            '',
            '',
            '',
            ''
          ]
        },
        { key: 'major', name: '' },
        { key: 'maritalStatus', name: '', type: 'selecteun', eun: ['', ''] },
        { key: 'monthlySalary', name: '', type: 'number' },
        { key: 'originalPosition', name: '' },
        { key: 'politicalOutlook', name: '' },
        { key: 'requiredGross', name: '', type: 'number' },
        { key: 'routeRequirements', name: '' },
        { key: 'shipRequirements', name: '' },
        { key: 'technicalSkill', name: '' },
        {
          key: 'userStatus',
          name: '',
          type: 'select',
          Arr: [
            { value: '0', label: '' },
            { value: '1', label: '' },
            { value: '2', label: '' }

          ]
        }
      ],
      form: {

      },
      url: '/seaman-recruit/resume/add'

    }
  },
  methods: {
    getData () {
      this.$axios.get('/seaman-recruit/resume/queryByUserId', '').then(res => {
        if (res.data.code === 200) {
          if (res.data.data && res.data.data.appellation) {
            this.form = res.data.data
            this.url = '/seaman-recruit/resume/updateById' // 
          }
        } else {
          console.log(res.data.data)
          this.$message.error(res.data.data)
        }
      })
    },
    BtnBack () {
      this.$router.back()
    },
    BtnClick () {
      this.$refs.form.validate((valid) => {
        if (valid) {
          const datas = JSON.parse(JSON.stringify(this.form))
          datas.userName = JSON.parse(localStorage.getItem('user')).userName
          datas.userId = JSON.parse(localStorage.getItem('user')).id
          datas.graduationTime = this.tiemFun(datas.graduationTime)
          this.$axios.post(this.url, datas).then(res => {
            if (res.data.code === 200) {
              this.$message({
                message: '',
                type: 'success'
              })
              this.$router.push('/home/user/JobList')
            } else {
              console.log(res.data.data)
              this.$message.error(res.data.data)
            }
          })
        } else {
          this.$message.error('')
        }
      })
    }
  },
  created () {
    this.getData()
  }
}
</script>

<style lang="scss" scoped>
.btn_l{
  margin-right: 40px;
}
.btn_r{
  margin-left: 40px;
}
.el-form-item{
  width: 50%;
  display:inline-block;
}
</style>
