<template>
  <div>
    <h1 style="margin-bottom: 20px;"></h1>
    <div v-if="form.statusFlag!==undefined" style="margin-bottom: 20px;">
      <span v-if="form.statusFlag===0"> </span>
      <span v-if="form.statusFlag===1" style="color:#67C23A"> </span>
      <span v-if="form.statusFlag===2" style="color:#F56C6C"> </span>
      </div>
  <el-form ref="form" :model="form" label-width="140px" :rules="rules">
      <el-form-item label="" prop="companyName">
    <el-input maxlength="50" v-model="form.companyName" :disabled="form.statusFlag===2"></el-input>
  </el-form-item>
      <el-form-item label="" prop="companyType">
    <el-input maxlength="30" v-model="form.companyType" :disabled="form.statusFlag===2"></el-input>
  </el-form-item>
      <el-form-item label="" prop="contactsName">
    <el-input maxlength="30" v-model="form.contactsName" :disabled="form.statusFlag===2"></el-input>
  </el-form-item>
      <el-form-item label="" prop="contactsPhone">
    <el-input maxlength="30" v-model="form.contactsPhone" :disabled="form.statusFlag===2"></el-input>
  </el-form-item>
      <el-form-item label="" prop="email">
    <el-input maxlength="100" v-model="form.email" :disabled="form.statusFlag===2"></el-input>
  </el-form-item>
      <el-form-item label="" prop="employeesNumber">
    <el-input maxlength="30" v-model="form.employeesNumber" :disabled="form.statusFlag===2"></el-input>
  </el-form-item>
      <el-form-item label="" prop="address">
    <el-input maxlength="130" v-model="form.address" :disabled="form.statusFlag===2"></el-input>
  </el-form-item>
      <el-form-item label="" prop="capital">
    <el-input maxlength="30" v-model="form.capital" :disabled="form.statusFlag===2" oninput ="value=value.replace(/[^0-9.]/g,'')"></el-input>
  </el-form-item>
      <el-form-item label="" prop="companyDesc">
    <el-input maxlength="500" v-model="form.companyDesc" :disabled="form.statusFlag===2"></el-input>
  </el-form-item>

      <el-form-item label="" prop="fax">
    <el-input maxlength="30" v-model="form.fax" :disabled="form.statusFlag===2"></el-input>
  </el-form-item>
      <el-form-item label="" prop="phone">
    <el-input maxlength="30" v-model="form.phone" :disabled="form.statusFlag===2"></el-input>
  </el-form-item>
      <el-form-item label="" prop="representative">
    <el-input maxlength="30" v-model="form.representative" :disabled="form.statusFlag===2"></el-input>
  </el-form-item>
      <el-form-item label="" prop="socialCreditCode">
    <el-input maxlength="50" v-model="form.socialCreditCode" :disabled="form.statusFlag===2"></el-input>
  </el-form-item>
      <el-form-item label="" prop="website">
    <el-input maxlength="30" v-model="form.website" :disabled="form.statusFlag===2"></el-input>
  </el-form-item>
      <el-form-item label="" prop="year">
         <el-date-picker :disabled="form.statusFlag===2"
                v-model="form.year"
                type="date" style="width: 100%;"
                placeholder="">
              </el-date-picker>
  </el-form-item>
  </el-form>
  <div style="    text-align: center;">
  <el-button class="btn_l" size="small" @click="BtnBack"></el-button>
  <el-button type="primary" size="small" class="btn_r" @click="BtnClick" :disabled="form.statusFlag==2"></el-button>
  </div>
   <el-divider></el-divider>
      <h1 > </h1>
      <el-upload
      class="avatar-uploader"
      action="http://172.81.209.8:9527/seaman-recruit/test/fileUrl"
      :show-file-list="false"
      :http-request="uploadFile"
      :on-success="handleAvatarSuccess"
      :before-upload="beforeAvatarUpload">
      <img v-if="imageUrl" :src="imageUrl" referrerpolicy="no-referrer" class="avatar">
      <i v-else class="el-icon-plus  avatar-uploader-icon"></i>
    </el-upload>
  </div>
</template>

<script>
export default {
  data () {
    return {
      form: {
        companyName: '',
        companyType: '',
        contactsName: '',
        contactsPhone: '',
        email: '',
        employeesNumber: '',
        address: '',
        capital: '',
        fax: '',
        phone: '',
        representative: '',
        socialCreditCode: '',
        website: '',
        year: ''
      },
      rules: {
        companyName: [{ required: true, message: '', trigger: 'change' }],
        companyType: [{ required: true, message: '', trigger: 'change' }],
        contactsName: [{ required: true, message: '', trigger: 'change' }],
        contactsPhone: [{ required: true, message: '', trigger: 'change' }],
        email: [{ required: true, message: '', trigger: 'change' }],
        employeesNumber: [{ required: true, message: '', trigger: 'change' }],
        address: [{ required: true, message: '', trigger: 'change' }],
        capital: [{ required: true, message: '', trigger: 'change' }],
        companyDesc: [{ required: true, message: '', trigger: 'change' }],
        fax: [{ required: true, message: '', trigger: 'change' }],
        phone: [{ required: true, message: '', trigger: 'change' }],
        representative: [{ required: true, message: '', trigger: 'change' }],
        socialCreditCode: [{ required: true, message: '', trigger: 'change' }],
        website: [{ required: true, message: '', trigger: 'change' }],
        year: [{ required: true, message: '', trigger: 'change' }]

      },
      imageUrl: JSON.parse(localStorage.getItem('user')).headPortrait ? 'http://' + JSON.parse(localStorage.getItem('user')).headPortrait : '',
      url: '/seaman-recruit/company/add'
    }
  },
  created () {
    this.getData()
  },
  methods: {

    handleAvatarSuccess (res, file) {
      this.imageUrl = URL.createObjectURL(file.raw)
    },
    beforeAvatarUpload (file) {
      const isJPG = file.type === 'image/jpeg'
      const isLt2M = file.size / 1024 / 1024 < 2

      if (!isJPG) {
        this.$message.error(' JPG !')
      }
      if (!isLt2M) {
        this.$message.error(' 2MB!')
      }
      console.log(file)
    },
    uploadFile (params) {
      const formdata = new FormData()
      const file = params.file
      formdata.append('file', file)
      this.$axios.post('/seaman-recruit/user/pictureUpload', formdata).then(res => {
        if (res.data.code === 200) {
          this.imageUrl = 'http://' + res.data.data
          this.$message({
            message: '',
            type: 'success'
          })
        } else {
          this.$message.error(res.data.data)
        }
      })
    },
    tiem (chinaStandard) {
      var date = new Date(chinaStandard)
      var y = date.getFullYear()
      var m = date.getMonth() + 1
      m = m < 10 ? ('0' + m) : m
      var d = date.getDate()
      d = d < 10 ? ('0' + d) : d
      const time = y + '-' + m + '-' + d

      return time
    },
    BtnBack () {
      this.$router.back()
    },
    getData () {
      this.$axios.get('/seaman-recruit/company/queryByUserId/', JSON.parse(localStorage.getItem('user')).id).then(res => {
        if (res.data.code === 200) {
          if (res.data.data && res.data.data.length > 0) {
            this.form = res.data.data[0]
            // debugger
            this.url = '/seaman-recruit/company/updateById' // 
          }
        } else {
          console.log(res.data.data)
          this.$message.error(res.data.data)
        }
      })
    },
    BtnClick () {
      this.$refs.form.validate((valid) => {
        if (valid) {
          this.form.year = this.tiem(this.form.year)
          this.form.userId = JSON.parse(localStorage.getItem('user')).id
          this.$axios.post(this.url, this.form).then(res => {
            if (res.data.code === 200) {
              this.$message({
                message: '',
                type: 'success'
              })
            } else {
              console.log(res.data.data)
              this.$message.error(res.data.data)
            }
          })
        } else {
          this.$message.error('')
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
  ::v-deep.avatar-uploader .el-upload {
    margin-top: 20px;
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
.btn_l{
  margin-right: 40px;
}
.btn_r{
  margin-left: 40px;
}
.el-form-item{
  width: 50%;
  display:inline-block;
}
</style>
