<template>
  <div>
    <div class="value_box " style="margin-bottom: 20px;">
      <div class="value_box">
        <span></span> <el-input class="w200" v-model="OptionData.userName"  placeholder=""></el-input>
      </div>
       <div class="value_box">
        <span></span> <el-input class="w200" v-model="OptionData.email"  placeholder=""></el-input>
      </div>
       <el-button type="primary" @click="getData"></el-button>
    </div>
  <el-table
    :data="tableData"
    style="width: 100%"
    >
    <el-table-column min-width="150"
    show-overflow-tooltip  prop="createdTime"
      label=""  align="center"
      >
       <template slot-scope="item">
         <div>{{item.row.createdTime|converTimeOfHMS}}</div>
      </template>
    </el-table-column>
    <el-table-column
      show-overflow-tooltip      prop="userName"  align="center"
      label=""
      >
    </el-table-column>
    <el-table-column
        show-overflow-tooltip      prop="userType"  align="center"
      label=""
      >
       <template slot-scope="item">
       <div>{{item.row.userType==0?'':item.row.userType==1?'':''}}</div>
      </template>
    </el-table-column>
    <el-table-column
      show-overflow-tooltip      prop="email"  align="center"
      label=""
      >
    </el-table-column>

     <el-table-column show-overflow-tooltip label=""  width="200" align="center">
      <template slot-scope="item">

          <el-button @click="open(item.row.id)" type="danger"
          size="mini"
          ></el-button>
      </template>
    </el-table-column>
  </el-table>
  <div class="fy">
    <el-pagination
  background @current-change="pageClick"
  layout="prev, pager, next"
  :total="total">
</el-pagination>
  </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      total: 0,
      tableData: [

      ],
      OptionData: {
        pageNum: 1,
        pageSize: 10
      }
    }
  },
  created () {
    this.getData()
  },
  methods: {
    pageClick (value) {
      this.OptionData.pageNum = value
      this.getData()
    },
    open (value) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.$axios.post('/seaman-recruit/user/delete', [value]).then(res => {
          if (res.data.code === 200) {
            this.$message({
              type: 'success',
              message: '!'
            })
            this.getData()
          } else {
            this.$message.error(res.data.data)
          }
        })
      }).catch(() => {

      })
    },
    getData () {
      this.$axios.post('/seaman-recruit/user/queryByPage', this.OptionData).then(res => {
        if (res.data.code === 200) {
          this.tableData = res.data.data.records
          this.total = res.data.data.total
        } else {
          console.log(res.data.data)
          this.$message.error(res.data.data)
        }
      })
    },
    formatter (row, column) {
      return row.address
    }
  }
}
</script>

<style lang="scss" scoped>
.value_box{
  display: flex;
  align-items: baseline;
}

::v-deep.el-table th.el-table__cell{
  background: #F8F8F9;
}
.w200{
  width: 140px;
  margin: 0 10px;
}
</style>
