<template>
  <div>
    <h1 style="margin-bottom: 20px;">{{$route.query.id?'':''}}</h1>
  <el-form ref="form" :model="form" :rules="rules" label-width="120px" style="width: 100%;">
      <el-form-item label="" prop="positionName">
        <el-select v-model="form.positionName" class="w100" placeholder="">
    <el-option
      v-for="item in AddArr"
      :key="item"
      :label="item"
      :value="item">
    </el-option>
  </el-select>
  </el-form-item>
      <el-form-item label="" prop="education">
         <el-select v-model="form.education" class="w100" placeholder="">
    <el-option
      v-for="item in EducationEnum"
      :key="item"
      :label="item"
      :value="item">
    </el-option>
  </el-select>
  </el-form-item>
      <el-form-item label="" prop="monthlySalary">
    <el-input maxlength="30" v-model="form.monthlySalary" oninput ="value=value.replace(/[^0-9.]/g,'')"></el-input>
  </el-form-item>
      <el-form-item label="()" prop="shipAge">
    <el-input maxlength="30" v-model="form.shipAge"></el-input>
  </el-form-item>
      <el-form-item label="" prop="shipType">
   <el-select v-model="form.shipType" class="w100" placeholder="">
    <el-option
      v-for="item in getShipTypeList"
      :key="item"
      :label="item"
      :value="item">
    </el-option>
  </el-select>
  </el-form-item>
      <el-form-item label="" prop="specificRoute">
    <el-input maxlength="60" v-model="form.specificRoute"></el-input>
  </el-form-item>
      <el-form-item label="()" prop="contractDuration">
    <el-input maxlength="10" v-model="form.contractDuration"></el-input>
  </el-form-item>
      <el-form-item label="" prop="boardingPlace">
    <el-input maxlength="30" v-model="form.boardingPlace"></el-input>
  </el-form-item>
      <el-form-item label="" prop="positionNum">
    <el-input maxlength="30" v-model="form.positionNum" oninput ="value=value.replace(/[^0-9.]/g,'')"></el-input>
  </el-form-item>
      <el-form-item label="/" prop="grossTon">
    <el-input maxlength="30" v-model="form.grossTon" oninput ="value=value.replace(/[^0-9.]/g,'')"></el-input>
  </el-form-item>
  </el-form>
  <div style="    text-align: center;">
  <el-button class="btn_l" size="small" @click="BtnBack"></el-button>
  <el-button type="primary" size="small" class="btn_r" @click="BtnClick"></el-button>
  </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      form: {

      },
      rules: {
        positionName: [
          { required: true, message: '', trigger: 'change' }
        ],
        education: [{ required: true, message: '', trigger: 'change' }],
        monthlySalary: [{ required: true, message: '', trigger: 'change' }],
        shipAge: [{ required: true, message: '()', trigger: 'change' }],
        shipType: [{ required: true, message: '', trigger: 'change' }],
        specificRoute: [{ required: true, message: '', trigger: 'change' }],
        contractDuration: [{ required: true, message: '()', trigger: 'change' }],
        boardingPlace: [{ required: true, message: '', trigger: 'change' }],
        positionNum: [{ required: true, message: '', trigger: 'change' }],
        grossTon: [{ required: true, message: '/', trigger: 'change' }]
      },
      Url: '/seaman-recruit/position/add'
    }
  },
  created () {
    if (this.$route.query.id) {
      this.getData()
      this.Url = '/seaman-recruit/position/updateById'
    }
  },
  methods: {
    BtnBack () {
      this.$router.back()
    },
    getData () {
      this.$axios.get('/seaman-recruit/position/queryById', '?id=' + this.$route.query.id).then(res => {
        if (res.data.code === 200) {
          this.form = res.data.data
        } else {
          this.$message.error(res.data.data)
        }
      })
    },
    BtnClick () {
      this.$refs.form.validate((valid) => {
        if (valid) {
          const datas = JSON.parse(JSON.stringify(this.form))
          datas.userName = JSON.parse(localStorage.getItem('user')).userName
          datas.userId = JSON.parse(localStorage.getItem('user')).id
          this.$axios.post(this.Url, datas).then(res => {
            if (res.data.code === 200) {
              this.$message({
                message: '',
                type: 'success'
              })
              this.$router.go(-1)
            } else {
              this.$message.error(res.data.data)
            }
          })
        } else {
          this.$message.error('')
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.btn_l{
  margin-right: 40px;
}
.btn_r{
  margin-left: 40px;
}
.el-form-item{
  width: 50%;
  display:inline-block;
}
</style>
